import { useState } from "react";
// import { useDispatch } from "react-redux";
// import { useNavigate } from "react-router-dom";
//import { useSelector } from 'react-redux';
import {
  VisibilityIcon,
  VisibilityOffIcon,
  CheckboxIcon,
} from "../../icons/icons";
import css from "./AuthForm.module.scss";
import { Link } from "react-router-dom";
import { ErrorMessage, Form, Formik, Field } from "formik";
import { schemaSignUp } from "../../helpers/schemes";
//import { register } from '../../redux/auth/operations';

const initialValues = {
  firstName: "",
  lastName: "",
  email: "",
  password: "",
  policy: false,
  remember: false,
};

export const RegisterForm = () => {
  // const navigete = useNavigate();
  // const dispatch = useDispatch();

  const [passwordShow, setPasswordShow] = useState(false);

  const togglePassword = () => {
    setPasswordShow(!passwordShow);
  };

  //   const handleSubmit = async (formData, { resetForm }) => {
  //    // const { error } = await login(formData);
  //     if (error) {
  //       setIsError({
  //         message: error.data.message,
  //         additionalInfo: error.data.additionalInfo,
  //       });
  //       resetForm();
  //       return;
  //     } else {
  //       navigate('/user');
  //     }
  //   };

  const handleSubmit = async (formData, { resetForm }) => {
    console.log("formData", formData);

    const newUser = {
      firstName: formData.firstName,
      lastName: formData.lastName,
      email: formData.email,
      password: formData.password,
      policy: formData.policy,
      remember: formData.remember,
    };
    console.log("newUser", newUser);
    resetForm();
    // dispatch(addNewUser(newUser));
    // navigete('/');
    return;
  };

  return (
    <>
      <Formik
        validationSchema={schemaSignUp}
        initialValues={initialValues}
        onSubmit={handleSubmit}
      >
        {(props) => (
          <Form className={css.form}>
            <h2 className={css.title}>Sign Up</h2>

            <div className={css.input__wrapper}>
              <label htmlFor="firstName" className={css.label}>
                First name{" "}
              </label>
              <Field
                className={css.input}
                name="firstName"
                type="text"
                //    placeholder="First Name"

                required
              />
              <ErrorMessage
                name="firstName"
                component="p"
                className={css.error}
              />
            </div>

            <div className={css.input__wrapper}>
              <label htmlFor="lastName" className={css.label}>
                Last name{" "}
              </label>
              <Field
                className={css.input}
                name="lastName"
                type="text"
                //    placeholder=""

                required
              />
              <ErrorMessage
                name="lastName"
                component="p"
                className={css.error}
              />
            </div>

            <div className={css.input__wrapper}>
              <label htmlFor="email" className={css.label}>
                E-mail
              </label>
              <Field
                className={css.input}
                name="email"
                type="email"
                //    placeholder=""

                required
              />
              <ErrorMessage name="email" component="p" className={css.error} />
            </div>

            <div className={css.input__wrapper}>
              <label htmlFor="password" className={css.label}>
                Password
              </label>
              <Field
                className={css.input}
                name="password"
                type={passwordShow ? "text" : "password"}
                //  placeholder="Password"

                required
              />
              <span
                id="visibilityBtn"
                className={css.iconPassword}
                onClick={togglePassword}
              >
                {passwordShow ? <VisibilityIcon /> : <VisibilityOffIcon />}
              </span>
              <ErrorMessage
                name="password"
                component="p"
                className={css.error}
              />
           
              {props.errors.password && (
                <a href="/" className={css.error_password}>
                  Forgot password?
                </a>
              )}
            </div>

            <div className={css.checkbox__wrapper}>
              <label className={css.checkbox}>
                {props.values.policy ? (
                  <div className={css.checkbox__icon_true}>
                    <CheckboxIcon />
                  </div>
                ) : (
                  <div className={css.checkbox__icon_false}>
                    <CheckboxIcon />
                  </div>
                )}

                <Field
                  className={css.checkbox__field}
                  type="checkbox"
                  name="policy"
                  required
                />

                <span className={css.checkbox__text}>
                  I agree to the processing of my data.
                </span>
              </label>

              <label className={css.checkbox}>
                {props.values.remember ? (
                  <div className={css.checkbox__icon_true}>
                    <CheckboxIcon />
                  </div>
                ) : (
                  <div className={css.checkbox__icon_false}>
                    <CheckboxIcon />
                  </div>
                )}
                <Field
                  className={css.checkbox__field}
                  type="checkbox"
                  name="remember"
                />
                <span className={css.checkbox__text}>Remember me</span>
              </label>
            </div>

            <button type="submit" className={css.button}>
              Sign Up
            </button>

            <p className={css.redirect_link}>
              <Link to="/login" className={css.link}>
                Already have an account? Log in
              </Link>
            </p>
          </Form>
        )}
      </Formik>
    </>
  );
};
